.wrappersite {
  width: 100%;
  height: auto;
}

.header {
  width: 100%;
  height: auto;
  box-shadow: 0 #00000040;
  background-image: url("background-header.110fae3e.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  padding-bottom: 50px;
  position: relative;
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .header {
    max-height: 635px;
  }
}

@media screen and (min-width: 900px) and (max-width: 1600px) {
  .header {
    background-size: 100% 100%;
  }
}

.header__nav {
  max-width: 1170px;
  border-bottom: 1px solid #babbc5;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.menu, .menu-burger, .menu-bg {
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 100%;
  font-size: 30px;
  position: absolute;
  top: 25px;
  right: 25px;
}

.menu__ul {
  flex-direction: column;
  align-items: flex-start;
  margin-top: 100px;
  display: flex;
}

.menu-bg {
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 50px;
  right: 50px;
}

.menu-burger {
  color: #fff;
  cursor: pointer;
  transform-origin: center;
  z-index: 2;
  padding-top: 11px;
  transition: all .4s;
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 900px) {
  .menu-burger {
    display: block;
  }
}

.menu-items {
  color: #fff;
  width: 100%;
  text-align: center;
  opacity: 0;
  pointer-events: none;
  margin-top: 100px;
  font-size: 40px;
  font-weight: bold;
  transition: all .4s;
  position: absolute;
  transform: translateY(-200%);
}

.menu-items ul {
  list-style-type: none;
}

.menu-items div {
  opacity: 0;
  margin-top: 0;
  transition: all 1s;
}

.menu-items.fs {
  pointer-events: auto;
  opacity: 1;
  width: 100%;
  height: 100%;
  background-image: url("background-header.110fae3e.png");
  background-size: cover;
  margin: 0 auto;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(0);
}

.menu-items.fs li {
  opacity: 1;
  margin-top: 10px;
}

.header__nav-menu {
  width: 670px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

@media (max-width: 900px) {
  .header__nav-menu {
    display: none;
  }
}

.menu__item {
  border-bottom: 3px solid #fff0;
}

.menu__item:hover {
  border-bottom: 3px solid #babbc5;
}

.menu__item:active {
  color: red;
  border-bottom: 3px solid red;
}

.header__nav-menu-li {
  width: 134px;
  vertical-align: middle;
  text-align: center;
  border-bottom: 3px solid #fff0;
  line-height: 100px;
}

.header__nav-menu-li:hover {
  width: 134px;
  border-bottom: 3px solid #babbc5;
}

.header__nav-menu-li:hover:active {
  border-color: red;
}

.header__nav-menu-li:hover a:active {
  color: red;
}

.header__nav-menu a {
  text-transform: uppercase;
  color: #fff;
  font-family: Raleway, sans-serif;
  font-size: 13px;
  font-weight: 700;
  text-decoration: none;
  display: block;
}

.nav__logo {
  width: 192px;
  height: 100px;
  z-index: 2;
  padding-left: 20px;
}

.nav__logo-burger {
  width: 192px;
  height: 100px;
  z-index: 2;
  float: left;
  padding-left: 20px;
}

.header__content {
  max-width: 1170px;
  min-height: 350px;
  margin: 100px auto;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header__content {
    margin: 0 auto;
  }
}

.header__content-h {
  padding: 0 100px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header__content-h {
    padding: 0 20px;
  }
}

.header__content-p {
  padding: 0 100px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header__content-p {
    padding: 0 20px;
  }
}

.header__content-btn {
  width: 370px;
  height: 80px;
  text-align: center;
  background-color: #ff4343;
  margin: 80px auto 0;
  line-height: 80px;
  display: block;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header__content-btn {
    width: 280px;
  }
}

.header__content-btn:hover {
  background-color: #e71414;
  animation: bounce 1s;
}

.header__content-btn:active {
  box-shadow: 4px 4px 30px #ff4343;
}

.bounce {
  transform-origin: bottom;
  animation-name: bounce;
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transition-timing-function: cubic-bezier(.215, .61, .355, 1);
    transform: translate3d(0, 0, 0);
  }

  40%, 43% {
    transition-timing-function: cubic-bezier(.755, .05, .855, .06);
    transform: translate3d(0, -30px, 0);
  }

  70% {
    transition-timing-function: cubic-bezier(.755, .05, .855, .06);
    transform: translate3d(0, -15px, 0);
  }

  90% {
    transform: translate3d(0, -4px, 0);
  }
}

